.client {
  padding-bottom: 264px;
  margin: 0 auto;
}
.wrapper {
  background-color: var(--light-grey);
  padding-top: 32px;
  padding-bottom: 64px;
}

.clientContainer {
  position: relative;
  /* max-width: 360px; */
  width: 100%;
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.text {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  text-align: center;
  margin-bottom: 32px;
  width: 250px;
}
.btnClient {
  max-width: 220px;
}

