@media (min-width: 768px) {
  .client {
    padding-bottom: 238px;
  }
  .wrapper {
    /* padding: 137px 0; */
    padding-top: clamp(5.625rem, 3.667rem + 4.08vw, 8.563rem);
    padding-bottom: clamp(5.625rem, 3.667rem + 4.08vw, 8.563rem);
  }
  .clientContainer {
    width: 100%;

    justify-content: flex-start;
    margin: 0 auto;
  }
  .text {
    /* font-size: 28px; */
    font-size: clamp(1.25rem, 0.917rem + 0.694vi, 1.75rem);
    line-height: 1.14;
    /* width: 650px; */
    width: clamp(20rem, 6.25rem + 28.646vw, 40.625rem);
    margin-bottom: 60px;
    text-align: left;
  }

  .clientContainer {
    margin: 0;
    align-items: flex-start;
  }
  .clientImg {
    /* width: 456px; */
    width: clamp(18.75rem, 12.25rem + 13.542vw, 28.5rem);
    top: -32px;
    /* left: 145%; */
    right: 0;
    transform: translateX(0%);
  }
}
